<script>
  import Vue from 'vue';
  export default {
    name: "SjCustomTile",
    props: {
        templateStr: {
            type: String,
            default: '',
        },
        cssStr: {
            type: String,
            default: '',
        },
        title: {
            type: String,
            default: 'TITLE',
        },
        desc: {
            type: String,
            default: "MY DESC",
        },
        length: {
            type: String,
            default: '1',
        },
        imgSrc: {
            type: String,
            default: 'https://placekitten.com/400/200',
        }
    },
    render(createElement) {
      if (this.templateStr === "") {
        return;
      }

      let template = `<div>
			${this.templateStr}
            <component is="style">
			    ${this.cssStr}
            </component>
        </div>`;
      // template = template.replace('[my-image]', '<my-image></my-image>');
      // template = template.replace('[my-description]', '<my-description></my-description>');
      // template = template.replace('[my-title]', '<my-title></my-title>');

      var component = Vue.component('dummy', {
        template: template,
        data: () => {
          return {
            length: this.length,
            imgSrc: this.imgSrc,
            desc: this.desc,
            title: this.title,
          }
        },
      });

      return createElement(
        'div',
        [
          createElement(component)
        ]
      )
    }
  }
</script>

